"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";

export default function DigitalProductGenerator() {
  const [niche, setNiche] = useState("");
  const [audience, setAudience] = useState("");
  const [category, setCategory] = useState("");
  const [loading, setLoading] = useState(false);
  const [productIdea, setProductIdea] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const generateProduct = async () => {
    setLoading(true);
    setError(null);
    setProductIdea(null);

    try {
      // Simulate API call to generate product idea based on niche data
      // Replace with actual API call to AI service
      await new Promise((resolve) => setTimeout(resolve, 1500));

      const idea = `Digital product idea for niche "${niche}", audience "${audience}", category "${category}": An innovative eBook series tailored to ${audience} in the ${niche} niche.`;
      setProductIdea(idea);
    } catch (err) {
      setError("Failed to generate product idea. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-3xl mx-auto p-4">
      <Card>
        <CardHeader>
          <CardTitle>Generate Digital Product</CardTitle>
          <CardDescription>
            Enter niche data points to generate a digital product idea.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            <div>
              <label htmlFor="niche" className="block text-sm font-medium text-gray-700">
                Niche Keywords
              </label>
              <input
                id="niche"
                type="text"
                value={niche}
                onChange={(e) => setNiche(e.target.value)}
                className="input mt-1 block w-full"
                placeholder="e.g., fitness, wellness"
              />
            </div>
            <div>
              <label htmlFor="audience" className="block text-sm font-medium text-gray-700">
                Target Audience
              </label>
              <input
                id="audience"
                type="text"
                value={audience}
                onChange={(e) => setAudience(e.target.value)}
                className="input mt-1 block w-full"
                placeholder="e.g., busy professionals"
              />
            </div>
            <div>
              <label htmlFor="category" className="block text-sm font-medium text-gray-700">
                Product Category
              </label>
              <input
                id="category"
                type="text"
                value={category}
                onChange={(e) => setCategory(e.target.value)}
                className="input mt-1 block w-full"
                placeholder="e.g., eBook, course"
              />
            </div>
            <div>
              <Button onClick={generateProduct} disabled={loading || !niche || !audience || !category}>
                {loading ? "Generating..." : "Generate Product Idea"}
              </Button>
            </div>
            {error && <p className="text-red-600">{error}</p>}
            {productIdea && (
              <div className="mt-4 p-4 border rounded bg-gray-50 dark:bg-gray-800">
                <h3 className="text-lg font-semibold mb-2">Generated Product Idea:</h3>
                <p>{productIdea}</p>
              </div>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
