"use client";

import { useState } from "react";
import { useMutation } from "@tanstack/react-query";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { AIContentRequest, AIContentResponse } from "@/services/ai-service";
import { LoadingSpinner, ErrorMessage } from "@/components/loading";

export function AIContentGenerator() {
  const [topic, setTopic] = useState("");
  const [type, setType] = useState<AIContentRequest["type"]>("article");

  const { mutate, isPending, data, error } = useMutation({
    mutationFn: async (request: AIContentRequest) => {
      const response = await fetch("/api/ai", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(request),
      });

      if (!response.ok) {
        throw new Error("Failed to generate content");
      }

      return response.json() as Promise<AIContentResponse>;
    },
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    mutate({ topic, type });
  };

  return (
    <Card className="w-full hover:shadow-lg transition-shadow animate-fade-in">
      <CardHeader>
        <CardTitle>AI Content Generator</CardTitle>
        <CardDescription>
          Generate high-quality content for your niche market
        </CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Input
              placeholder="Enter your topic..."
              value={topic}
              onChange={(e) => setTopic(e.target.value)}
              disabled={isPending}
              className="glass-effect"
            />
          </div>
          <div className="space-y-2">
            <Select
              value={type}
              onValueChange={(value) => setType(value as AIContentRequest["type"])}
              disabled={isPending}
            >
              <SelectTrigger className="glass-effect">
                <SelectValue placeholder="Select content type" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="article">Article</SelectItem>
                <SelectItem value="analysis">Market Analysis</SelectItem>
                <SelectItem value="competitor">Competitor Analysis</SelectItem>
              </SelectContent>
            </Select>
          </div>
          <Button 
            type="submit" 
            disabled={isPending || !topic}
            className="w-full sm:w-auto hover:scale-105 transition-transform"
          >
            {isPending ? "Generating..." : "Generate Content"}
          </Button>

          {error && (
            <ErrorMessage 
              message={error instanceof Error ? error.message : "An error occurred"} 
            />
          )}

          {isPending && <LoadingSpinner />}

          {data && !isPending && (
            <div className="mt-8 space-y-4 animate-fade-in">
              <div className="prose max-w-none dark:prose-invert">
                <h3 className="text-lg font-semibold">Generated Content:</h3>
                <div className="whitespace-pre-wrap rounded-lg bg-muted p-4 glass-effect">
                  {data.content}
                </div>
                {data.metadata && (
                  <div className="text-sm text-muted-foreground mt-2">
                    Word count: {data.metadata.wordCount}
                  </div>
                )}
              </div>
            </div>
          )}
        </form>
      </CardContent>
    </Card>
  );
}
